.new-expense {
  background-color: var(--main-light-green);
  padding: 1rem;
  margin: 2rem auto;
  width:90%;
  /* width: 50rem;
  max-width: 95%; */
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid var(--main-green);
  background-color: var(--main-green);
  color: var(--main-white);
  border-radius: 12px;
  margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: var(--main-dark-green);
  border-color: var(--main-dark-green);
}

.new-expense button.alternative {
  color: var(--main-grey);
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: var(--main-light-green);
}
